@import url(https://fonts.googleapis.com/css?family=Roboto);
@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    font-family: Roboto;
}

.login {
  cursor: pointer;
  font-family: Roboto;
  font-size: larger;
  font-weight: bold;
}

.modal-container {
  background-color: white;
  position: absolute;
  margin-top: 30px;
  border-radius: 10px;
  border: none;
}

.modal-overlay--center {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-content: center;
  background-color: hsla(100, 4%, 15%, 0.185);
  z-index: 99;
}

.signup_box {
  display: flex;
  flex-direction: row-reverse;
  margin: 5px;
}

/* LOGIN */
.home-login {
  display: flex;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
}

.home-signup {
  height: 470px;
  margin: 0px 50px 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
}

.home-login img {
  width: 70px;
  margin-top: 10px;
}

.home-signup img {
  width: 70px;
}

.home-form {
  height: 300px;
  margin: 10px 50px;
  display: flex;
  flex-direction: column;
}

.home-signup-form {
  height: 600px;
  padding-top: 5px;
  display: flex;
  flex-direction: column;
}

.home-signup-form div {
  padding-top: 30px;
  color: rgba(173, 166, 166, 1);
  font-size: large;
  font-family: roboto;
}

.home-signup-form input {
  margin-top: 10px;
  width: 300px;
  height: 40px;
  padding-left: 5px;
  background-color: #ece7e7;
  border: none;
  border-radius: 5px;
}

.home-form div {
  padding-top: 30px;
  color: rgba(173, 166, 166, 1);
  font-size: large;
  font-family: roboto;
}

.home-form input {
  margin-top: 10px;
  width: 300px;
  height: 40px;
  padding-left: 5px;
  background-color: #ece7e7;
  border: none;
  border-radius: 5px;
}

.home-form-submit {
  margin-top: 50px;
  padding: 10px;
  font-family: roboto;
  font-size: medium;
  border: none;
  border-radius: 5px;
  color: #ece7e7;
  background-color: #fe024e;
}

.home-login-redirect {
  padding: 40px;
  font-family: roboto;
  color: #8c8989;
  font-size: medium;
}

.redirect-signup {
  cursor: pointer;
  font-weight: bold;
  color: #fe024e;
}

.home-login-btn {
  padding: 2px;
  border: none;
  background-color: white;
  color: #8c8989;
  font-family: roboto;
  font-size: x-large;
  margin-left: 370px;
  margin-top: 2px;
}

.img-logo {
  padding: inherit;
}

.form {
  padding: inherit;
}

.search {
  padding: inherit;
}

.dropdown img {
  width: 40px;
}

/* RESPONSIVE */
@media screen and (max-width: 450px) {
  .home-signup-form input {
    margin-top: 10px;
    width: 200px;
    height: 40px;
    padding-left: 5px;
    background-color: #ece7e7;
    border: none;
    border-radius: 5px;
  }

  .home-form input {
    margin-top: 10px;
    width: 200px;
    height: 40px;
    background-color: #ece7e7;
    border: none;
    border-radius: 5px;
  }
}

/*FOOTER*/
.footer {
  width: 100%;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  background-color:black;
  color: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer-container {
  width: 75%;
  display: flex;
  justify-content: space-around;
  margin-top: 40px;
}

.footer-logo {
  width: 300px;
}

.footer-logo p {
  padding-top: 10px;
  font-size: 20px;
  opacity: 0.9;
}

.footer-logo img {
  width: 100px;
}

.footer-nav li {
  list-style: none;
  font-size: 20px;
  padding-top: 10px;
  opacity: 0.9;
}

.social p {
  font-size: 20px;
  font-weight: bold;
  padding: 6px;
  padding-left: 11px;
}

.social-download {
  width: 400px;
  display: flex;
  justify-content: space-between;
}

.social-download img {
  width: 50%;
}

.appStore {
  padding-top: 6px;
}

.social-media {
  display: flex;
  justify-content: space-between;
  width: 80px;
}

.footer .social-media img {
  width: 70%;
  padding-left: 11px;
  border-radius: 10px;
}

.copyright {
  margin-top: 30px;
  font-size: 15px;
}

/* responsive */
/* ukuran tablet */
@media screen and (max-width: 768px) {
  .footer {
      width: 100%;
      height: 100%;
      flex-wrap: wrap;
  }
}

/* ukuran mobile */
@media screen and (max-width: 576px) {
  .footer {
    width: 100%;
    height: 100%;
  }
}
.img-card-review {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  object-fit: cover;
}

.img-card-character {
  width: 150px;
  height: 150px;
  border-radius: 5px;
  object-fit: cover;
}

.card-cast-movie {
  display: flex;
  flex-wrap: wrap;
}

.name-cast {
  white-space: nowrap;
  width: 150px;
  overflow: hidden;
  text-overflow: ellipsis;
}

.review-form {
  max-width: 500px;
}

